import React from "react";
import { useParams } from "react-router-dom";
import { getBlog } from "../api/Blogs";
import useSWR from "swr";
import Header from "../components/Header";
import Footer from "../components/Footer";
import Skeleton from "react-loading-skeleton";
import "react-loading-skeleton/dist/skeleton.css";
import Helmet from "react-helmet";

export default function Blog() {
  const params = useParams();
  const { data: blog, error } = useSWR(
    `https://news-api.aakhyaan.org/wp-json/wp/v2/posts/${params.id}`,
    (url) => getBlog(url), { revalidateOnFocus: false, revalidateOnReconnect: true }
  );
  return (
    <div
      className={`w-full h-full bg-back pt-20 dark:bg-background dark:text-mainText/90`}
    >
      <Helmet>
        <title>Blog | Aakhyaan Academy</title>
      </Helmet>
      <Header page="blog" />
      {!blog && !error ? (
        <>
          <div className="flex flex-col py-10 px-[10%] bg-back dark:bg-background h-full">
            <div className="mx-auto w-28">
              <Skeleton className="" height={25} baseColor="gray" />
            </div>
            <div className="flex flex-col gap-1 mt-10">
              {[...Array(15)].map((val, i) => (
                <div key={i} className="w-full mx-auto">
                  <Skeleton className="" height={20} baseColor="silver" />
                </div>
              ))}
            </div>
          </div>
          <Footer />
        </>
      ) : (
        <>
          <div className="w-full h-full py-10 px-[10%] bg-back dark:bg-background">
            <div
              dangerouslySetInnerHTML={{ __html: blog?.title?.rendered }}
              className="text-center text-4xl"
            ></div>
            <div
              className="mt-10"
              dangerouslySetInnerHTML={{ __html: blog?.content?.rendered }}
            ></div>
          </div>
          <Footer />
        </>
      )}
    </div>
  );
}
